/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  background-color: #23C1E8;
}
.header__grid {
  padding: 0 0 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__title {
  margin: 0 0 0 1rem;
}
.header__logo {
  width: 120px;
  height: 120px;
}
.header__image {
  border-radius: 50%;
  border: white solid 0.5rem;
}
.header__navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    margin-top: 0;
    flex-direction: row;
  }
}
.header__enlace {
  font-weight: 900;
  font-size: 2.5rem;
  color: #2B2F36;
}
.header__enlace:hover {
  font-weight: 900;
  font-size: 2.5rem;
  color: #FFD261;
}

.languages {
  padding: 1rem 0 2rem 0;
  text-align: right;
}
@media (min-width: 768px) {
  .languages {
    padding-bottom: 0;
  }
}

.en {
  color: #002663;
}

.es {
  color: #F2BA01;
}

.check {
  position: relative;
  width: 50px;
  height: 17px;
}

.check:before {
  content: "";
  position: absolute;
  width: 50px;
  height: 25px;
  background: #002663;
  border-radius: 25px;
}

.check:after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  background: #fff;
  border-radius: 25px;
  transition: 0.25s;
  border: 2px solid #333;
  box-sizing: border-box;
}

.check:checked:after {
  left: 25px;
  border: 2px solid #F2BA01;
}

.check:checked:before {
  background: #F2BA01;
}

.hero__grid {
  margin-top: 2rem;
  display: grid;
  grid-template-rows: repeat(3, 30rem);
  min-height: 35rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .hero__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(3, 1fr);
  }
}
.hero__first {
  background-image: url(/build/img/misscarmela.webp);
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  border-radius: 10%;
}
.hero__second {
  background-image: url(/build/img/galeria1.webp);
  background-size: cover;
  background-position: center;
  border-radius: 10%;
  background-repeat: no-repeat;
}
.hero__third {
  background-image: url(/build/img/aprendiendoaescribir.webp);
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  border-radius: 10%;
}

.info {
  background-color: #F2F2F2;
  margin: 4rem 0;
  padding: 4rem 0;
}
.info__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .info__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.info__imagen {
  background-image: url(/build/img/galeria3.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .info__imagen {
    order: 1;
  }
}
.info__texto {
