.message {
  margin: 4rem;
}
.message__chat {
  border: gray solid 0.2rem;
  height: 17rem;
  border-radius: 2rem;
}
.message__text {
  height: 80%;
  width: 99%;
  margin: 2rem auto;
  text-overflow: ellipsis;
  overflow: scroll;
  overflow-x: hidden;
}
.message__text::-webkit-scrollbar {
  width: 10px;
}
.message__text::-webkit-scrollbar-track {
  background: white;
}
.message__text::-webkit-scrollbar-thumb {
  background: gray;
}

